* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  background: url(/static/media/worldMap5.f8cbafa4.svg) no-repeat center center fixed;
  background-size: cover;
  
}

.slide-in-left-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-in-left-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.main-header {
  width: 100%;
  height: 6rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #c314319b, #240b3696);
  /* box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26); */
  padding: 0 1rem;
  z-index: 5;
  transition: all 0.3s ease-out 0s !important;
  box-shadow: 0px 10px 10px 0px rgba(128, 125, 141, 0.171) !important;
  -webkit-backdrop-filter: blur(7px) !important;
          backdrop-filter: blur(7px) !important;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
}
.nav-links {
  list-style: none;
  margin: 0;
  /* padding-right: 5%; */
  font-family: Comfortaa;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #ffffff;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #f8df0000;
  /* border-color: #292929; */
  color: #b6fbff;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #d900ff4d;
  color: #ffffff;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #ffffff;
    color: #292929;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: linear-gradient(to right, #c3143188, #240b3678); ;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

@font-face {
  font-family: Handwritting;
  src: url(/static/media/HelloKetta-d99oX.0f052441.ttf) format("truetype");
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__motto{
  position: relative;
  font-family: Handwritting;
  font-size: 30px;
  font-weight: 200;
  line-height: 2px;
  letter-spacing: 1px;
}


.main-navigation__title {
  padding-left: 5%;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
  padding-right: 5%;
  padding-top: 1%;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

/* Title Animation */
svg {
  height: 50px;
  font-weight: 800;
  display: -webkit-flex;
  display: flex;
  font: 7.3em "Montserrat";
  width: 200px;
  margin: 0 auto;
  padding: -10%;
}

.text-copy {
  fill: none;
  stroke: white;
  stroke-dasharray: 6% 29%;
  stroke-width: 5px;
  stroke-dashoffset: 0%;
  -webkit-animation: stroke-offset 5.5s infinite linear;
          animation: stroke-offset 5.5s infinite linear;
}

.text-copy:nth-child(1){
stroke: #caf7e3;
-webkit-animation-delay: -1;
        animation-delay: -1;
}

.text-copy:nth-child(2){
stroke: #edffec;
-webkit-animation-delay: -2s;
        animation-delay: -2s;
}

.text-copy:nth-child(3){
stroke: #f6dfeb;
-webkit-animation-delay: -3s;
        animation-delay: -3s;
}

.text-copy:nth-child(4){
stroke: #e4bad4;
-webkit-animation-delay: -4s;
        animation-delay: -4s;
}

.text-copy:nth-child(5){
stroke: #b4aee8;
-webkit-animation-delay: -5s;
        animation-delay: -5s;
}

@-webkit-keyframes stroke-offset{
100% {stroke-dashoffset: -35%;}
}

@keyframes stroke-offset{
100% {stroke-dashoffset: -35%;}
}

.main-navigation__drawer-nav {
  color: white;
  font-weight: 300;
}
.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(214, 222, 243, 0.815);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.boxes {
  height: 32px;
  width: 32px;
  position: relative;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  margin-top: 32px;
  -webkit-transform: rotateX(60deg) rotateZ(45deg) rotateY(0deg) translateZ(0px);
  transform: rotateX(60deg) rotateZ(45deg) rotateY(0deg) translateZ(0px);
}
.boxes .box {
  width: 32px;
  height: 32px;
  top: 0px;
  left: 0;
  position: absolute;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}



.boxes .box:nth-child(1) {
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
  -webkit-animation: box1 1s linear infinite;
  animation: box1 1s linear infinite;
}
.boxes .box:nth-child(2) {
  -webkit-transform: translate(0, 100%);
  transform: translate(0, 100%);
  -webkit-animation: box2 1s linear infinite;
  animation: box2 1s linear infinite;
}
.boxes .box:nth-child(3) {
  -webkit-transform: translate(100%, 100%);
  transform: translate(100%, 100%);
  -webkit-animation: box3 1s linear infinite;
  animation: box3 1s linear infinite;
}
.boxes .box:nth-child(4) {
  -webkit-transform: translate(200%, 0);
  transform: translate(200%, 0);
  -webkit-animation: box4 1s linear infinite;
  animation: box4 1s linear infinite;
}



.boxes .box > div {
  background: #5C8DF6;
  --translateZ: 15.5px;
  --rotateY: 0deg;
  --rotateX: 0deg;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #5C8DF6;
  top: auto;
  right: auto;
  bottom: auto;
  left: auto;
  -webkit-transform: rotateY(var(--rotateY)) rotateX(var(--rotateX)) translateZ(var(--translateZ));
  transform: rotateY(var(--rotateY)) rotateX(var(--rotateX)) translateZ(var(--translateZ));
}

.boxes .box > div:nth-child(1) {
  top: 0;
  left: 0;
  background: #5C8DF6;
}
.boxes .box > div:nth-child(2) {
  background: #145af2;
  right: 0;
  --rotateY: 90deg;
}
.boxes .box > div:nth-child(3) {
  background: #447cf5;
  --rotateX: -90deg;
}
.boxes .box > div:nth-child(4) {
  background: #dbe3f44d;
  top: 0;
  left: 0;
  --translateZ: -90px;
}





@-webkit-keyframes box1 {
  0%,
  50% {
      -webkit-transform: translate(100%, 0);
              transform: translate(100%, 0);
  }
  100% {
      -webkit-transform: translate(200%, 0);
              transform: translate(200%, 0);
  }
}





@keyframes box1 {
  0%,
  50% {
      -webkit-transform: translate(100%, 0);
              transform: translate(100%, 0);
  }
  100% {
      -webkit-transform: translate(200%, 0);
              transform: translate(200%, 0);
  }
}

@-webkit-keyframes box2 {
  0%{
      -webkit-transform: translate(0, 100%);
              transform: translate(0, 100%);
  }
  50% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
  }
  100% {
      -webkit-transform: translate(100%, 0);
              transform: translate(100%, 0);
  }
}

@keyframes box2 {
  0%{
      -webkit-transform: translate(0, 100%);
              transform: translate(0, 100%);
  }
  50% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
  }
  100% {
      -webkit-transform: translate(100%, 0);
              transform: translate(100%, 0);
  }
}

@-webkit-keyframes box3 {
  0%,
  50% {
      -webkit-transform: translate(100%, 100%);
              transform: translate(100%, 100%);
  }
  100% {
      -webkit-transform: translate(0, 100%);
              transform: translate(0, 100%);
  }
}

@keyframes box3 {
  0%,
  50% {
      -webkit-transform: translate(100%, 100%);
              transform: translate(100%, 100%);
  }
  100% {
      -webkit-transform: translate(0, 100%);
              transform: translate(0, 100%);
  }
}

@-webkit-keyframes box4 {
  0%{
      -webkit-transform: translate(200%, 0);
              transform: translate(200%, 0);
  }
  50% {
      -webkit-transform: translate(200%, 100%);
              transform: translate(200%, 100%);
  }
  100% {
      -webkit-transform: translate(100%, 100%);
              transform: translate(100%, 100%);
  }
}

@keyframes box4 {
  0%{
      -webkit-transform: translate(200%, 0);
              transform: translate(200%, 0);
  }
  50% {
      -webkit-transform: translate(200%, 100%);
              transform: translate(200%, 100%);
  }
  100% {
      -webkit-transform: translate(100%, 100%);
              transform: translate(100%, 100%);
  }
}
